.catalog__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
  grid-gap: 30px;
  grid-auto-flow: dense;
  max-width: 870px;
  padding: 0;
  margin: 0;
  margin-bottom: 50px;
  list-style: none;

  @media (max-width: 1200px) {
    max-width: 800px;
  }

  @media (max-width: 670px) {
    display: flex;
    flex-direction: column;
  }
}

.catalog__show-more {
  display: none;
  margin-left: 9px;
  margin-right: 9px;

  @media (max-width: 760px) {
    display: block;
    margin-bottom: 50px;
  }
}

.catalog__show-button {
  border: 0;
  background-color: #f0f0f4;
  font-weight: 600;
  font-size: 15px;
  line-height: 18px;
  text-align: center;
  letter-spacing: .12em;
  text-transform: uppercase;
  color: #bdbec2;
  width: 100%;
  padding: 17px 0 14px;
}

.catalog__pagination {
  margin-bottom: 100px;
  margin-left: 15px;

  @media (max-width: 767px) {
    margin-left: 0;
    margin-bottom: 80px;
  }
}

.catalog__pagination-list {
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  width: 335px;
  margin: 0 auto;

  @media (max-width: 767px) {
    width: 100%;
    justify-content: space-between;
  }
}

.catalog__pagination-item {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #a8a8a8;
  padding-top: 9px;
  padding-bottom: 5px;
  margin-right: 20px;
  margin-left: 22px;

  &:hover {
    opacity: 0.5;
  }

  &:active,
  &:focus {
    opacity: 0.7;
  }

  a {
    text-decoration: none;
    color: #a8a8a8;
  }

  @media (max-width: 767px) {
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    margin: 0;
  }
}

.catalog__pagination-item--current {
  border: 2px solid #ff7931;
  padding: 8px 14px 4px;
  color: #ff7931;
  font-weight: 600;
  margin-right: 20px;
  margin-left: 0;

  &:hover,
  &:focus {
    opacity: 1;
  }

  a {
    cursor: default;
    color: #ff7931;
  }

  @media (max-width: 767px) {
    margin: 0;
  }
}

.catalog__pagination-item--delimiter {
  padding-left: 0;
  padding-right: 0;
  align-self: flex-end;
  margin: 0;
}
